.Header{height:60px;background-color:#20232a;color:#ffffff;position:fixed;width:100%;top:0;left:0;box-shadow:0 2px 2px rgba(0,0,0,0.1);z-index:1000}.Header__list a{color:#ffffff}.user{display:flex;align-items:center;position:relative;cursor:pointer;font-size:15px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.user__avatar{width:35px;height:35px;text-align:center;background:#ffffff;border-radius:50px}.user__avatar i{line-height:35px;font-size:22px;color:#000000}.user__dropdown{position:absolute;background:#ffffff;color:#000000;top:120%;right:0;left:0;border-radius:4px;box-shadow:0 8px 6px -6px #000;z-index:1000}.user__dropdown ul{text-align:center;margin:0;padding:0}.user__dropdown li{color:#1e1e2d;padding:10px 30px;line-height:1;list-style:none}

#ModalRegister label{display:block;font-weight:500;font-size:14px}#ModalRegister input{display:block;width:100%;padding:8px 20px;border-radius:3px;box-shadow:none;border:1px solid #95a5a6}

.CardCourse{transition:transform .5s;cursor:pointer;margin-bottom:15px}.CardCourse a{color:#20232a;font-size:22px}.CardCourse::after{position:absolute;top:0;left:0;width:100%;height:100%;transition:opacity 2s cubic-bezier(0.165, 0.84, 0.44, 1);box-shadow:0 8px 17px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.15);content:'';opacity:0;z-index:-1}.CardCourse:hover,.CardCourse:focus{transform:scale3d(1.006, 1.006, 1)}.CardCourse:hover a,.CardCourse:focus a{text-decoration:none}.CardCourse:hover::after,.CardCourse:focus::after{opacity:1}.CardCourse__img{height:200px}.card-body{border-top:1px solid #eaeaea}

.Aside__Menu{position:fixed;width:100%;top:60px;left:0;z-index:2}.Aside__Menu .nav .nav-link{color:#495057;text-transform:uppercase;background-color:#fff;font-size:14px;border:none;border-radius:0;font-weight:600;text-align:center}.Aside__Menu .nav .nav-link:active,.Aside__Menu .nav .nav-link:focus{outline:none;box-shadow:none}.Aside__Menu .nav .nav-link.active{color:#ffffff;background:#495057}.Aside__Menu .nav .nav-item{flex:1 1}.Aside__Tool{text-align:center;position:fixed;width:25%;left:0;top:60px;height:calc(100vh - 60px);padding:50px 0;overflow-y:auto;background-color:#f7f7f7;border-right:1px solid #ececec;z-index:1}.Aside__Tool::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#F5F5F5}.Aside__Tool::-webkit-scrollbar{width:6px;background-color:#F5F5F5}.Aside__Tool::-webkit-scrollbar-thumb{background-color:#000000}.Aside__Tab{padding:20px}.Aside__Process{width:100px;margin:10px auto;height:100px}.Aside__List{margin-top:50px}.Aside__List .btn{padding-left:40px;display:inline-block;padding-right:40px;margin-bottom:5px}.Aside__List .btn .disabled{opacity:.6;pointer-events:none;user-focus:none}.Aside__List ul li.badge{padding:10px 20px;cursor:pointer;margin:3px}

.hint-button svg{
    vertical-align: bottom;
    margin-right: 5px;
}
.hint-button{
    font-weight: 500;
}
.hint-content{
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0,0,0,.125);
    border-radius: .25rem;
    padding: 1.25rem;
    margin-top: 10px;
}
.Content{
    margin-top: 60px;
    min-height: 100vh;
}
.Title{
    font-size: 1.7rem;
    color: #2980b9;
}
.Footer{text-align:center;position:relative;z-index:2;margin-top:auto;height:40px;line-height:40px}.Footer i{font-size:14px}

