.ArticleIntro{cursor:pointer;flex-direction:row;align-items:center;margin:15px 0 !important;padding:15px;box-shadow:0 8px 17px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.15)}.ArticleIntro .card-title{font-size:25px;font-weight:700;color:#5d78ff}.ArticleIntro .card-body{border-top:none}.ArticleIntro img{width:100px;height:100px}.ArticleIntro::before,.ArticleIntro::after{position:absolute;top:0;right:0;bottom:0;left:0;transition:opacity .38s ease-in-out, transform .35s ease-in-out;content:'';opacity:0;pointer-events:none}.ArticleIntro::before{transform:scale3d(0, 1, 1);transform-origin:left top;border-top:1px solid #acacac;border-bottom:1px solid #acacac}.ArticleIntro::after{transform:scale3d(1, 0, 1);transform-origin:right top;border-right:1px solid #acacac;border-left:1px solid #acacac}.ArticleIntro:hover::before,.ArticleIntro:hover::after,.ArticleIntro:focus::before,.ArticleIntro:focus::after{transform:scale3d(1, 1, 1);opacity:1}

